body {
  background: url(/portfolio2/static/media/background-pattern.b8c9b62f.png);
  font-family: 'Ubuntu', sans-serif; }

.nav-item :focus {
  outline: none; }

.nav-link {
  color: inherit;
  padding: 0px; }
  .nav-link:hover {
    color: inherit; }

.info {
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
  position: absolute;
  cursor: default;
  width: 90%;
}

.image {
  opacity: 1;
  transition: .5s ease;
}

.image-container:hover .image {
  opacity: 0.15;
}

.image-container:hover .info {
  opacity: 1;
}
